// src/services/api.js
import axios from 'axios';
import config from '../config/config';

// URL de base pour toutes les requtes API
const BASE_URL = config.API_URL;

console.log('API client initializing with base URL:', BASE_URL);

// Configuration optimise d'axios
const api = axios.create({
    baseURL: BASE_URL,
    timeout: 8000, // Timeout rduit  8 secondes pour viter les attentes trop longues
    headers: {
        'Content-Type': 'application/json'
    },
    // Utilisation d'un proxy pour contourner les problmes CORS en dveloppement
    proxy: false
});

// Nombre maximal de tentatives de reconnexion
const MAX_RETRIES = 1;

// Intercepteur pour ajouter le token d'authentification aux requtes
api.interceptors.request.use(
    (config) => {
        // Log minimal des requtes pour dbogage
        console.log(`API Request: ${config.method?.toUpperCase()} ${config.url}`);

        // Ajouter le token pour toutes les requtes authentifies
        const token = localStorage.getItem('token');
        if (token) {
            config.headers['Authorization'] = `Bearer ${token}`;
        }

        // Ajouter un paramtre pour viter la mise en cache des requtes GET
        if (config.method === 'get') {
            config.params = {
                ...config.params,
                _t: Date.now() // Timestamp unique
            };
        }

        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// Intercepteur pour grer les rponses et les erreurs
api.interceptors.response.use(
    (response) => {
        return response;
    },
    async (error) => {
        const originalRequest = error.config;

        // Information d'erreur pour le dbogage
        const errorInfo = {
            url: originalRequest?.url,
            method: originalRequest?.method,
            status: error.response?.status,
            message: error.message
        };

        // Vrification si la requte a dj t tente
        if (error.message.includes('timeout') &&
            originalRequest &&
            !originalRequest._retry &&
            originalRequest._retryCount < MAX_RETRIES) {

            // Incrmenter le compteur de tentatives
            originalRequest._retryCount = (originalRequest._retryCount || 0) + 1;
            console.log(`Retry attempt ${originalRequest._retryCount} for ${originalRequest.url}`);

            // Attendre un court dlai avant de ressayer
            await new Promise(resolve => setTimeout(resolve, 1000));

            // Ressayer la requte
            return api(originalRequest);
        }

        // Personnalisation des messages d'erreur
        if (error.message.includes('timeout')) {
            error.message = "La requte a pris trop de temps  rpondre. Le serveur est-il dmarr ?";
            console.error("Erreur de timeout:", errorInfo);
        } else if (error.code === 'ERR_NETWORK') {
            error.message = "Impossible de se connecter au serveur. Vrifiez que le serveur backend est dmarr.";
            console.error("Erreur rseau:", errorInfo);
        } else {
            console.error("Erreur API:", errorInfo);
        }

        return Promise.reject(error);
    }
);

export default api;