// src/contexts/AuthContext.js
import React, { createContext, useContext, useState, useEffect } from 'react';
import api from '../services/api';
import { jwtDecode } from 'jwt-decode';
import * as authService from '../services/authService';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const [adminStatus, setAdminStatus] = useState(false);

    // Fonction de dtection du statut administrateur adapte pour Sequelize
    const checkAdminStatus = (userData) => {
        if (!userData) return false;

        // Donner la priorit  la structure documente du backend (champ 'role')
        if (userData.role === 'admin') {
            console.log('Statut admin dtect via le champ role (Sequelize)');
            return true;
        }

        // Vrifications secondaires pour compatibilit
        const isAdminBoolean =
            userData.isAdmin === true ||
            userData.is_admin === true;

        // Vrification via les claims JWT
        if (userData.claims && userData.claims.isAdmin) {
            return true;
        }

        const result = isAdminBoolean;
        console.log('Vrification admin pour:', userData, 'Rsultat:', result);
        return result;
    };

    useEffect(() => {
        // Supprimer le token pour forcer la connexion  chaque dmarrage de l'application
        localStorage.removeItem('token');
        delete api.defaults.headers.common['Authorization'];

        // Vrifier si l'utilisateur est dj connect au chargement
        const checkLoggedIn = async () => {
            const token = localStorage.getItem('token');
            if (token) {
                try {
                    // Vrifier la validit du token
                    api.defaults.headers.common['Authorization'] = `Bearer ${token}`;

                    // Tentative d'obtenir les donnes utilisateur via /auth/me
                    const response = await api.get('/auth/me');
                    console.log('Donnes utilisateur reues de /auth/me:', response.data);

                    // Les donnes peuvent tre directement dans response.data ou dans response.data.user (Sequelize)
                    const userData = response.data.user || response.data;

                    // Vrifier et stocker le statut admin
                    const isUserAdmin = checkAdminStatus(userData);
                    setAdminStatus(isUserAdmin);

                    // Si l'utilisateur est admin mais que le champ n'est pas dfini, l'ajouter
                    if (isUserAdmin && userData.isAdmin === undefined) {
                        userData.isAdmin = true;
                    }

                    setUser(userData);

                    console.log('Utilisateur charg avec statut admin:', isUserAdmin);
                } catch (error) {
                    console.error("Erreur de vrification du token:", error);
                    localStorage.removeItem('token');
                    delete api.defaults.headers.common['Authorization'];
                    setAdminStatus(false);
                    setUser(null);
                }
            }
            setLoading(false);
        };

        checkLoggedIn();
    }, []);

    // Fonction de connexion
    const login = async (email, password) => {
        try {
            // Utiliser le service d'authentification mis  jour
            const authResult = await authService.login({ email, password });

            // Vrifier si la connexion a russi
            if (!authResult.success) {
                return {
                    success: false,
                    message: authResult.message || 'Identifiants incorrects. Veuillez ressayer.'
                };
            }

            // Extraction des donnes utilisateur
            const userData = authResult.user;

            if (!userData) {
                return {
                    success: false,
                    message: "Format de rponse invalide"
                };
            }

            // Configuration du client API avec le token (si ncessaire - normalement dj fait dans authService)
            if (authResult.token) {
                api.defaults.headers.common['Authorization'] = `Bearer ${authResult.token}`;
            }

            // Mise  jour des tats
            setUser(userData);
            setAdminStatus(authResult.isAdmin || false);

            console.log("Connexion russie, utilisateur dfini:", userData);
            console.log("Statut admin:", authResult.isAdmin);

            return { success: true };
        } catch (error) {
            console.error("Erreur de connexion:", error);

            return {
                success: false,
                message: error.message || "Une erreur inattendue s'est produite"
            };
        }
    };

    const register = async (userData) => {
        try {
            const response = await authService.register(userData);

            // Si l'API retourne directement un token, connecter l'utilisateur
            if (response.token) {
                const token = response.token;
                const userInfo = response.user || {};

                // Vrifier le statut admin
                const isUserAdmin = checkAdminStatus(userInfo);

                // Configurer le client API
                api.defaults.headers.common['Authorization'] = `Bearer ${token}`;

                // Mettre  jour les tats
                setUser(userInfo);
                setAdminStatus(isUserAdmin);

                return { success: true };
            }

            // Si pas de connexion automatique, simplement indiquer le succs
            return { success: true };
        } catch (error) {
            console.error("Erreur d'inscription:", error);
            return {
                success: false,
                message: error.response?.data?.message || "chec de l'inscription"
            };
        }
    };

    const logout = () => {
        localStorage.removeItem('token');
        delete api.defaults.headers.common['Authorization'];
        setUser(null);
        setAdminStatus(false);
    };

    const isAuthenticated = () => !!user;

    // Fonction amliore pour vrifier si l'utilisateur est administrateur
    const isAdmin = () => {
        // Utiliser l'tat ddi au statut admin comme source de vrit principale
        if (adminStatus === true) {
            return true;
        }

        // Si l'tat n'est pas dfini, vrifier l'objet user
        const adminFromUser = checkAdminStatus(user);

        // Si le statut est trouv dans user mais pas dans l'tat, mettre  jour l'tat
        if (adminFromUser && !adminStatus) {
            setAdminStatus(true);
        }

        return adminFromUser;
    };

    const value = {
        user,
        login,
        register,
        logout,
        isAuthenticated,
        isAdmin,
        loading,
        adminStatus // Exposer directement le statut admin pour les composants qui en ont besoin
    };

    return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth doit tre utilis  l\'intrieur d\'un AuthProvider');
    }
    return context;
};