// src/services/productionService.js
import api from './api';

// Cache simple pour viter les requtes redondantes
const cache = {
    productions: new Map(), // Map des productions par page/params
    productionDetails: new Map(), // Map des dtails de production par ID
    TTL: 2 * 60 * 1000, // TTL de 2 minutes (rduit pour le dveloppement)
};

// Fonction pour rcuprer toutes les productions avec filtres
export const getProductions = async (page = 1, options = {}) => {
    try {
        // Construction des paramtres de requte
        const params = { page, limit: options.limit || 9 };

        // Ajouter uniquement les filtres dont nous avons besoin
        if (options.search) params.search = options.search;
        // Envoi du genre mme s'il est vide (pour l'option "Tous")
        if (options.genre !== undefined) params.genre = options.genre;
        if (options.sortBy) params.sortBy = options.sortBy;
        if (options.releaseDateRange && options.releaseDateRange !== 'all') params.releaseDateRange = options.releaseDateRange;

        // Cration d'une cl de cache base sur les paramtres
        const cacheKey = JSON.stringify(params);
        console.log("Requte avec paramtres:", params);

        // Vrification du cache (dsactiv temporairement pour le debug)
        // const cachedData = cache.productions.get(cacheKey);
        // if (cachedData && (Date.now() - cachedData.timestamp < cache.TTL)) {
        //     console.log("Rsultats depuis le cache");
        //     return cachedData.data;
        // }

        // Utilisation de l'API avec le prfixe /api gr par l'instance api
        console.log("Appel API pour rcuprer les productions");
        const response = await api.get('/productions', { params });
        console.log("Rponse API reue:", response.data);

        // Mise en cache des donnes
        if (response.data) {
            cache.productions.set(cacheKey, {
                data: response.data,
                timestamp: Date.now()
            });
        }

        return response.data;
    } catch (error) {
        console.error('Erreur lors de la rcupration des productions:', error);
        throw error;
    }
};

// Fonction pour rcuprer une production par ID
export const getProductionById = async (id) => {
    try {
        // Vrification du cache
        const cachedData = cache.productionDetails.get(id);
        if (cachedData && (Date.now() - cachedData.timestamp < cache.TTL)) {
            return cachedData.data;
        }

        const response = await api.get(`/productions/${id}`);

        // Mise en cache des donnes
        if (response.data) {
            cache.productionDetails.set(id, {
                data: response.data,
                timestamp: Date.now()
            });
        }

        return response.data;
    } catch (error) {
        console.error(`Erreur lors de la rcupration de la production ${id}:`, error);
        throw error;
    }
};

// Fonction pour crer une nouvelle production
export const createProduction = async (productionData) => {
    try {
        // Vrifier si productionData est dj un FormData
        const formData = productionData instanceof FormData
            ? productionData
            : createFormDataFromObject(productionData);

        const response = await api.post('/productions', formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        });

        // Invalider le cache des productions aprs cration
        cache.productions.clear();

        return response.data;
    } catch (error) {
        console.error('Erreur lors de la cration de la production:', error);
        throw error;
    }
};

// Fonction utilitaire pour crer un FormData  partir d'un objet
const createFormDataFromObject = (data) => {
    const formData = new FormData();
    for (const key in data) {
        if (data[key] !== null && data[key] !== undefined) {
            formData.append(key, data[key]);
        }
    }
    return formData;
};

// Fonction pour mettre  jour une production existante
export const updateProduction = async (id, productionData) => {
    try {
        const formData = new FormData();

        // Ajouter chaque champ au formData
        for (const key in productionData) {
            if (productionData[key] !== null && productionData[key] !== undefined) {
                formData.append(key, productionData[key]);
            }
        }

        const response = await api.put(`/productions/${id}`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        });

        // Invalider les caches aprs mise  jour
        cache.productions.clear();
        cache.productionDetails.delete(id);

        return response.data;
    } catch (error) {
        console.error(`Erreur lors de la mise  jour de la production ${id}:`, error);
        throw error;
    }
};

// Fonction pour supprimer une production
export const deleteProduction = async (id) => {
    try {
        const response = await api.delete(`/productions/${id}`);

        // Invalider les caches aprs suppression
        cache.productions.clear();
        cache.productionDetails.delete(id);

        return response.data;
    } catch (error) {
        console.error(`Erreur lors de la suppression de la production ${id}:`, error);
        throw error;
    }
};

// Fonction pour nettoyer le cache
export const clearProductionsCache = () => {
    cache.productions.clear();
    cache.productionDetails.clear();
};
