import api from './api';

// Cache simple pour viter les requtes redondantes
const cache = {
    users: new Map(), // Map des utilisateurs par page/params
    userDetails: new Map(), // Map des dtails d'utilisateur par ID
    TTL: 2 * 60 * 1000, // TTL de 2 minutes
};

// Rcuprer tous les utilisateurs (pour l'admin)
export const getUsers = async (page = 1, limit = 10, searchTerm = '', sortBy = 'created_at', order = 'DESC') => {
  try {
    console.log('[CLIENT] Rcupration des utilisateurs avec params:', { page, limit, searchTerm, sortBy, order });

    // Cration d'une cl de cache base sur les paramtres
    const cacheKey = JSON.stringify({ page, limit, searchTerm, sortBy, order });

    // Vrification du cache
    const cachedData = cache.users.get(cacheKey);
    if (cachedData && (Date.now() - cachedData.timestamp < cache.TTL)) {
      console.log('[CLIENT] Utilisateurs rcuprs du cache');
      return cachedData.data;
    }

    // Calculer l'offset bas sur la page et la limite
    const offset = (page - 1) * limit;

    // Paramtres de requte adapts au backend
    const params = {
      limit,
      offset,
      search: searchTerm,
      sortBy,
      sortOrder: order
    };

    console.log('[CLIENT] Envoi requte API:', params);
    // Utiliser la route /users au lieu de /admin/users
    const response = await api.get('/users', { params });
    console.log('[CLIENT] Rponse API utilisateurs:', response.data ? response.data.length : 'aucune');

    // Mise en cache des donnes
    if (response.data) {
      cache.users.set(cacheKey, {
        data: response.data,
        timestamp: Date.now()
      });
    }

    return response.data;
  } catch (error) {
    console.error('[CLIENT] Erreur lors de la rcupration des utilisateurs:', error);
    throw error;
  }
};

// Rcuprer un utilisateur par son ID
export const getUserById = async (id) => {
  try {
    console.log(`[CLIENT] Rcupration de l'utilisateur ID: ${id}`);

    // Vrification du cache
    const cachedData = cache.userDetails.get(id);
    if (cachedData && (Date.now() - cachedData.timestamp < cache.TTL)) {
      return cachedData.data;
    }

    const response = await api.get(`/users/${id}`);

    // Mise en cache des donnes
    if (response.data) {
      cache.userDetails.set(id, {
        data: response.data,
        timestamp: Date.now()
      });
    }

    return response.data;
  } catch (error) {
    console.error(`[CLIENT] Erreur lors de la rcupration de l'utilisateur ${id}:`, error);
    throw error;
  }
};

// Mettre  jour un utilisateur
export const updateUser = async (id, userData) => {
  try {
    const response = await api.put(`/users/${id}`, userData);

    // Invalider les caches
    cache.users.clear();
    cache.userDetails.delete(id);

    return response.data;
  } catch (error) {
    console.error(`[CLIENT] Erreur lors de la mise  jour de l'utilisateur ${id}:`, error);
    throw error;
  }
};

// Changer le rle d'un utilisateur (admin/user)
export const changeUserRole = async (id, isAdmin) => {
  try {
    const response = await api.patch(`/users/${id}/role`, { isAdmin });

    // Invalider les caches
    cache.users.clear();
    cache.userDetails.delete(id);

    return response.data;
  } catch (error) {
    console.error(`[CLIENT] Erreur lors du changement de rle de l'utilisateur ${id}:`, error);
    throw error;
  }
};

